import { AfterViewChecked, AfterViewInit, ChangeDetectorRef, Component, OnDestroy } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import domToImage from "dom-to-image";
import { VhAlgorithm, VhAuth, VhImage, VhQueryCafe } from 'ionic-vhframeworks';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import printJS from 'print-js';
import { Observable } from 'rxjs';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-template26',
  templateUrl: './template26.component.html',
  styleUrls: ['./template26.component.scss']
})
export class Template26Component implements OnDestroy, AfterViewInit, AfterViewChecked {
  dataSave: any;
  imageWaiting: string[] = []
  editStatus: boolean = true;
  theme: "1" | "2" | "3" = "1"
  themeTemp: "1" | "2" | "3" = "1"

  color1: string = '#373737'
  color2: string = '#159347'

  dataMenu: Array<any> = [
    {
      _id: 0,
      titleMenu: {
        value: "MENU",
        style: {
          align: 'center',
          fontBold: true,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 72,
          fontColor: this.color2
        }
      },
      img: {
        src: "assets/icon/management/menu/template26/cover.jpg",
        shape: 'rectangle',
        ratio: 1
      },
      address: {
        value: "Địa chỉ: 161/3 Ni Sư Huỳnh Liên, p.10, Q. Tân Bình, Tp.HCM",
        style: {
          align: 'center',
          fontBold: false,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 14,
          fontColor: this.color1
        }
      },
      type: 'cover'
    },
    {
      _id: 1,
      data: [[], []],
      type: 'page'
    }
  ]
  titleMenu: any = {
    value: "Cafe Viethas",
    style: {
      align: 'center',
      fontBold: false,
      fontItalic: false,
      fontUnderline: false,
      font: 'Arial',
      fontSize: 32,
      fontColor: this.color2
    }
  }

  dataAttribute: any = null
  classRows: any = null;
  exportStatus: boolean = false;
  applyStatus: boolean = false
  saveStatus: boolean = false
  constructor(
    private cdRef: ChangeDetectorRef,
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private modal: NzModalService,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhImage: VhImage
  ) {
    this.dataSave = this.router.getCurrentNavigation().extras.state
    // kiểm tra bản lưu 
    if (this.dataSave) {
      this.theme = this.dataSave['theme']
      this.themeTemp = this.dataSave['theme']
      this.dataMenu[0] = this.dataSave['cover']
      this.titleMenu = this.dataSave['title']
    }
    this.getData()
  }

  myTrackByFunction(element, index) {
    return element.index ? element.index : index;
  }
  myTrackByFunctionID(element, index) {
    return element._id ? element._id : index;
  }



  ngAfterViewInit() {
    document.getElementById("template-container").addEventListener("fullscreenchange", (ev) => {
      this.editStatus = !this.editStatus
    })

    this.dataMenu = this.transformData(this.dataMenu)

    document.getElementById("mainTitle").focus()
  }

  ngOnDestroy() {
    document.getElementById("template-container").removeEventListener("fullscreenchange", (ev) => { })

    this.deleteImageWaiting(this.imageWaiting)
  }


  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }

  changeTheme(theme) {
    this.modal.confirm({
      nzTitle: this.languageService.translate("Are you sure to change this theme?"),
      nzContent: this.languageService.translate("Changing the theme will change your editing"),
      nzOkText: 'OK',
      nzCancelText: this.languageService.translate("Cancel"),
      nzOnOk: () =>
        new Promise((resolve, reject) => {
          this.theme = theme;
          switch (theme) {
            case "1":
              this.color1 = '#373737'
              this.color2 = '#159347'
              break
            case "2":
              this.color1 = '#373737'
              this.color2 = '#b8693d'
              break
            case "3":
              this.color1 = '#373737'
              this.color2 = '#1482a0'
              break
          }
          // set toàn bộ màu sắc
          this.titleMenu.style.fontColor = this.color2
          this.dataMenu.forEach((page, index) => {
            if (index > 0) {
              page.data[0].forEach(row => {
                row.style.fontColor = row.class == "category-lines" ? this.color2 : this.color1
              });
              page.data[1].forEach(row => {
                row.style.fontColor = row.class == "category-lines" ? this.color2 : this.color1
              });
            } else {
              page.titleMenu.style.fontColor = this.color2
              page.address.style.fontColor = this.color1
            }
          })
          resolve()
        }),
      nzOnCancel: () => {
        return new Promise((resolve) => {
          this.themeTemp = this.theme
          resolve()
        })
      }
    });
  }

  /**Render các thành phần combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }

  getData() {
    let categories = this.vhQueryCafe.getlocalCategorys(1)
    let foods = this.vhQueryCafe.getlocalProducts(1).filter(item => !item.delete_hidden)
    let ingredients = this.vhQueryCafe.getlocalProducts(3).filter(item => !item.delete_hidden)
    let sideDishes = this.vhQueryCafe.getlocalProducts(4).filter(item => !item.delete_hidden)
    let combos = this.vhQueryCafe.getlocalProducts(5).filter(item => !item.delete_hidden)
    let index = 0; // đánh dấu index món ăn
    let rowStyle: any;
    // kiểm tra có bản lưu hay không?
    if (this.dataSave) rowStyle = this.dataSave['rowStyle']

    // combo
    if (combos.length) {
      let styleCategory: any = rowStyle ? rowStyle['combo'] : null;
      this.dataMenu[1].data[0].push({
        name: this.languageService.translate("Combo"), class: 'category-lines', index, _id: 'combo',
        style: styleCategory ? styleCategory : {
          align: 'left',
          fontBold: true,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 14,
          fontColor: this.color2,
        }
      })
      index++;
      for (let combo of combos) {
        let styleFood: any = rowStyle ? rowStyle[combo._id] : null;
        this.dataMenu[1].data[0].push({
          name: combo.name, price: combo.price, class: 'food-lines-price', index, _id: combo._id,
          style: styleFood ? styleFood : {
            align: 'left',
            fontBold: false,
            fontItalic: false,
            fontUnderline: false,
            font: 'Arial',
            fontSize: 12,
            fontColor: this.color1
          }
        })
        index++;

        let styleSub: any = rowStyle ? rowStyle[`subs-${combo._id}`] : null;
        this.dataMenu[1].data[0].push({
          name: this.getSubCombo(combo.combos), class: 'combo-subs-lines', index, _id: `subs-${combo._id}`,
          style: styleSub ? styleSub : {
            align: 'left',
            fontBold: false,
            fontItalic: false,
            fontUnderline: false,
            font: 'Arial',
            fontSize: 10,
            fontColor: this.color1
          }
        })
        index++;
      }
    }
    for (let category of categories) {
      let styleCategory: any = rowStyle ? rowStyle[category._id] : null;

      this.dataMenu[1].data[0].push({
        name: category.name, class: 'category-lines', index, _id: category._id,
        style: styleCategory ? styleCategory : {
          align: 'left',
          fontBold: true,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 14,
          fontColor: this.color2
        }
      })

      index++;

      for (let food of foods) {
        if (food.id_category == category._id) {
          if (food.subs && food.subs.length > 0) {
            let styleFood: any = rowStyle ? rowStyle[food._id] : null;
            this.dataMenu[1].data[0].push({
              name: food.name, class: 'food-lines-noprice', index, _id: food._id,
              style: styleFood ? styleFood : {
                align: 'left',
                fontBold: false,
                fontItalic: false,
                fontUnderline: false,
                font: 'Arial',
                fontSize: 12,
                fontColor: this.color1
              }
            })
            index++;
            for (let subs of food.subs) {
              let styleSub: any = rowStyle ? rowStyle[subs._id] : null;
              this.dataMenu[1].data[0].push({
                name: subs.name, price: subs.price, class: 'subs-lines', index, _id: subs._id,
                style: styleSub ? styleSub : {
                  align: 'left',
                  fontBold: false,
                  fontItalic: false,
                  fontUnderline: false,
                  font: 'Arial',
                  fontSize: 12,
                  fontColor: this.color1
                }
              })
              index++;
            }
          } else {
            let styleFood: any = rowStyle ? rowStyle[food._id] : null;
            this.dataMenu[1].data[0].push({
              name: food.name, price: food.price, class: 'food-lines-price', index, _id: food._id,
              style: styleFood ? styleFood : {
                align: 'left',
                fontBold: false,
                fontItalic: false,
                fontUnderline: false,
                font: 'Arial',
                fontSize: 12,
                fontColor: this.color1
              }
            })
            index++;
          }
        }
      }
    }

    // món phụ cho bán
    if (sideDishes.length) {
      let styleCategory: any = rowStyle ? rowStyle['sideDish'] : null;
      this.dataMenu[1].data[0].push({
        name: this.languageService.translate("Side dishes"), class: 'category-lines', index, _id: 'sideDish',
        style: styleCategory ? styleCategory : {
          align: 'left',
          fontBold: true,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 14,
          fontColor: this.color2,
        }
      })
      index++;
      for (let sideDish of sideDishes) {
        let styleSideDishes: any = rowStyle ? rowStyle[sideDish._id] : null;
        this.dataMenu[1].data[0].push({
          name: sideDish.name, price: sideDish.price, class: 'food-lines-price', index, _id: sideDish._id,
          style: styleSideDishes ? styleSideDishes : {
            align: 'left',
            fontBold: false,
            fontItalic: false,
            fontUnderline: false,
            font: 'Arial',
            fontSize: 12,
            fontColor: this.color1
          }
        })
        index++;
      }
    }

    // nguyên liệu cho bán
    if (ingredients.length) {
      let styleCategory: any = rowStyle ? rowStyle['ingredient'] : null;
      this.dataMenu[1].data[0].push({
        name: this.languageService.translate("Ingredient"), class: 'category-lines', index, _id: 'ingredient',
        style: styleCategory ? styleCategory : {
          align: 'left',
          fontBold: true,
          fontItalic: false,
          fontUnderline: false,
          font: 'Arial',
          fontSize: 14,
          fontColor: this.color2,
        }
      })
      index++;
      for (let ingredient of ingredients) {
        let styleIngredient: any = rowStyle ? rowStyle[ingredient._id] : null;
        this.dataMenu[1].data[0].push({
          name: ingredient.name, price: ingredient.price, class: 'food-lines-price', index, _id: ingredient._id,
          style: styleIngredient ? styleIngredient : {
            align: 'left',
            fontBold: false,
            fontItalic: false,
            fontUnderline: false,
            font: 'Arial',
            fontSize: 12,
            fontColor: this.color1
          }
        })
        index++;
      }
    }
  }
  transformData(dataMenuRaw: any) {
    let data: any[] = [
      this.dataMenu[0],
      {
        _id: 1,
        data: [[], []],
        type: 'page'
      }
    ]
    let i: number = this.dataMenu.length - 1;
    let j: number = 0;
    let countUnit: number = 0;
    const transfer = (distance: number) => {
      countUnit += distance
      if (countUnit > 567 && j == 0) {
        // dư thừa đưa sang cột kế bên
        let temp = data[i].data[j].pop()
        j += 1;
        countUnit = distance
        data[i].data[j].push(temp)
      }

      if (countUnit > 567 && j == 1) {
        // dư thừa đưa sang trang kế tiếp
        let temp = data[i].data[j].pop()
        i++;
        j = 0;
        countUnit = distance
        data.push({
          _id: i,
          data: [[temp], []],
          type: 'page'
        })
      }
    }

    for (let rows of dataMenuRaw[1].data[0]) {
      let node = document.getElementById("page-row-" + rows.index)
      data[i].data[j].push(rows)
      transfer(node.clientHeight)
    }

    return data;
  }

  // phân trang sau khi đã load dữ liệu
  async changeStyle(returnData?: { style: any, class: string }) {
    this.applyStatus = true
    let countUnit: number = 0;
    const transfer = async (pageIdCurrent: number, columnCurrent: number, rowCurrent: number, distance: number) => {
      // lấy dữ liệu trang tiếp theo đưa về lại trang đầu
      const fetchDataNextPage: any = () => {
        if (rowCurrent == numberItem - 1) {// cuối cột nhưng chưa lấp đầy cột
          if (this.dataMenu[pageIdCurrent + 1]) { // TH đổ dữ liệu trang tiếp theo về lại trang cũ 
            if (this.dataMenu[pageIdCurrent + 1].data[0].length > 0) { // nếu cột 1 trang tiếp còn rows
              let temp: any = this.dataMenu[pageIdCurrent + 1].data[0].shift()
              if (temp) this.dataMenu[pageIdCurrent].data[columnCurrent].push(temp)
              // console.log("page " + pageIdCurrent + " - " + rowCurrent + " " + countUnit)
            } else if (this.dataMenu[pageIdCurrent + 1].data[1].length > 0) { // nếu cột 2 trang tiếp còn rows
              let temp: any = this.dataMenu[pageIdCurrent + 1].data[1].shift()
              if (temp) this.dataMenu[pageIdCurrent].data[columnCurrent].push(temp)
            } else {
              // console.log("xóa trang thừa " + pageIdCurrent + " - " + rowCurrent + " " + countUnit)
              this.dataMenu.splice(pageIdCurrent + 1, 1); // xóa các trang thừa
              // kiểm tra lại một lần nữa xem còn trang thừa nào không?
              transfer(pageIdCurrent, columnCurrent, rowCurrent, 0)
            }
          }
        }
      }

      countUnit += distance
      let numberItem: number = this.dataMenu[pageIdCurrent].data[columnCurrent].length
      if (columnCurrent == 0) {
        if (countUnit > 567) {
          // dư thừa đưa sang cột kế bên
          let temp = this.dataMenu[pageIdCurrent].data[0].splice(rowCurrent, numberItem - rowCurrent)
          this.dataMenu[pageIdCurrent].data[1] = temp.concat(this.dataMenu[pageIdCurrent].data[1])
          countUnit = 0
        } else {
          if (rowCurrent == numberItem - 1) {// cuối cột nhưng chưa lấp đầy cột
            if (this.dataMenu[pageIdCurrent].data[1].length > 0) { // nếu cột 2 tiếp theo có rows
              let temp: any = this.dataMenu[pageIdCurrent].data[1].shift()
              if (temp) this.dataMenu[pageIdCurrent].data[0].push(temp)
            } else if (this.dataMenu[pageIdCurrent].data[1].length > 0) { // nếu cột 3 tiếp theo có rows
              let temp: any = this.dataMenu[pageIdCurrent].data[1].shift()
              if (temp) this.dataMenu[pageIdCurrent].data[0].push(temp)
            } else fetchDataNextPage()
          }
        }
      }

      if (columnCurrent == 1) {
        if (countUnit > 567) {
          let temp: any[] = this.dataMenu[pageIdCurrent].data[1].splice(rowCurrent, numberItem - rowCurrent)
          // chuyển dữ liệu thừa sang page tiếp theo
          if (this.dataMenu[pageIdCurrent + 1]) // có tồn tại page 2
            this.dataMenu[pageIdCurrent + 1].data[0] = temp.concat(this.dataMenu[pageIdCurrent + 1].data[0])
          else {
            this.dataMenu.push({
              _id: this.dataMenu.length,
              data: [temp, []],
              type: 'page'
            })
          }
          countUnit = 0
        } else fetchDataNextPage()
      }
    }

    for (let i = 1; i < this.dataMenu.length; i++) {
      for (let j = 0; j < this.dataMenu[i].data.length; j++) {
        for (let k = 0; k < this.dataMenu[i].data[j].length; k++) {
          if (returnData) {
            if (returnData.class == this.dataMenu[i].data[j][k].class) {
              this.dataMenu[i].data[j][k].style = { ...returnData.style, outline: 'none' }
              await this.timeout(10)
            }
          }
          let node = document.getElementById("page-row-" + this.dataMenu[i].data[j][k].index)
          // đang kiểm tra rows đang xét
          this.dataMenu[i]._id = this.dataMenu[i - 1]._id + 1  //set lại id sau nhiều lần xóa các trang thừa
          transfer(i, j, k, node.clientHeight)
        }
      }
    }

    if (returnData) this.outOfElement()
    this.applyStatus = false
  }

  renderSize(size: "A4" | "A3" | "A5") {
    switch (size) {
      case "A3": return "11px"
      case "A5": return "25px"
      default: return "17px"
    }
  }

  print() {
    this.outOfElement()
    this.exportStatus = true

    setTimeout(async () => {
      let data = new Array(this.dataMenu.length)
      for (let [index, item] of this.dataMenu.entries()) {
        data[index] = await domToImage.toSvg(document.getElementById("menu-book-" + item._id), { quality: 1 })
      }

      printJS({
        printable: data,
        type: 'image',
        imageStyle: 'width: 100%',
        style: '@page { margin: 0 !important; padding: 0 !important; }'
      })
      this.exportStatus = false
    }, 10)
  }

  preview() {
    this.outOfElement()
    document.getElementById("template-container").requestFullscreen()
  }
  // chỉnh sửa
  edit(style: any, rowClass?: string) {
    if (this.dataAttribute) {
      this.outOfElement()
      style.outline = '1px black dashed'
      this.dataAttribute = style
      this.classRows = rowClass
    } else {
      style.outline = '1px black dashed'
      setTimeout(() => {
        this.dataAttribute = style
        this.classRows = rowClass
      }, 200)
    }
  }
  outOfElement() {
    // xóa dữ liệu Input
    if (this.dataAttribute) {
      this.dataAttribute.outline = 'none'
      this.dataAttribute = null
    }
  }

  timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**Lưu menu */
  saveMenu() {
    return new Promise<boolean>((resolve, rejects) => {
      this.saveStatus = true
      let rowStyle: Object = new Object({})
      // lưu trữ style các dòng
      for (let i = 1; i < this.dataMenu.length; i++) {
        for (let j = 0; j < this.dataMenu[i].data.length; j++) {
          this.dataMenu[i].data[j].forEach(row => {
            rowStyle[row._id] = row.style
          });
        }
      }

      let data: any = {
        rowStyle,
        id_editor: this.vhAuth.getUser()._id,
        template: 26,
        pattern: 11,
        date: (new Date()).toISOString(),
        theme: this.theme,
        cover: this.dataMenu[0],
        title: this.titleMenu,
      }

      if (this.dataSave) {
        this.vhQueryCafe.updatePatternMenu(this.dataSave._id, data).then(result => {
          // reset lại ban đầu
          this.imageWaiting = []
          this.saveStatus = false
          resolve(true)
        }).catch(err => rejects(err))
      } else {
        this.vhQueryCafe.addPatternMenu(data).then(result => {
          // reset lại ban đầu
          this.imageWaiting = []
          this.dataSave = result
          this.saveStatus = false
          resolve(true)
        }).catch(err => rejects(err))
      }
    })
  }

  /**Check Exit Page */
  exitPage: boolean = false;
  popUpExit() {
    return new Promise<{ save: boolean, exit: boolean }>(resolve => {
      const modal: NzModalRef = this.modal.create({
        nzTitle: this.languageService.translate("This menu has not been saved yet"),
        nzContent: this.languageService.translate("Do you want to exit?"),
        nzFooter: [
          {
            label: this.languageService.translate("Save & Exit"),
            type: 'text',
            loading: this.saveStatus,
            onClick: () => {
              modal.destroy()
              resolve({ save: true, exit: true })
            }
          },
          {
            label: this.languageService.translate("Cancel"),
            onClick: () => {
              modal.destroy()
              resolve({ save: false, exit: false })
            }
          },
          {
            label: this.languageService.translate("Exit"),
            type: 'primary',
            onClick: () => {
              modal.destroy()
              resolve({ save: false, exit: true })
            }
          }
        ]
      })
    })
  }
  goBack() {
    this.popUpExit().then(({ save, exit }) => {
      this.exitPage = exit;
      if (exit) {
        if (save) {
          this.saveMenu().then(() => this.router.navigateByUrl("/cafe/dashboard/management/design/design-menu"))
        } else this.router.navigateByUrl("/cafe/dashboard/management/design/design-menu")
      }
    })
  }

  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (!this.exitPage) {
      this.popUpExit().then(({ save, exit }) => {
        this.exitPage = exit;
        if (exit) {
          if (save) {
            this.saveMenu().then(() => this.router.navigateByUrl(nextState.url))
          } else this.router.navigateByUrl(nextState.url)
        }
      })
    } return this.exitPage;
  }
  /**Lưu tạm các ảnh đã up nhưng chưa lưu menu */
  addImageWaiting(photoURL) {
    this.imageWaiting.push(photoURL.newSrc)
    // xóa url cũ 
    let index = this.imageWaiting.findIndex(item => item == photoURL.oldSrc)
    if (index != -1) this.imageWaiting.splice(index, 1)
  }
  /**Xóa ảnh lưu tạm vì không lưu menu */
  deleteImageWaiting(images: any[]) {
    if (images.length) {
      this.vhImage.deleteThumbnail(images)
    }
  }
}
